import { Component, OnInit } from '@angular/core';
import { NgxGalleryOptions } from '@kolkov/ngx-gallery';
import { NgxGalleryImage } from '@kolkov/ngx-gallery';
import { NgxGalleryAnimation, NgxGalleryImageSize } from '@kolkov/ngx-gallery';
import { CommonService } from 'src/app/service/common.service';
import { ProductStateService } from 'src/app/service/product-state.service';

declare var $: any;
@Component({
    selector: 'product-detail',
    templateUrl: './product-detail.component.html',
    styleUrls: ['./product-detail.component.scss']
})
export class ProductDetailComponent implements OnInit {

    galleryOptions: NgxGalleryOptions[];
    galleryImages: NgxGalleryImage[];
    productObj: any;
    sizeList: any;
    selectedSize: any;
    selectSizeError:Boolean;
    objectKeys = Object.keys;
    constructor(private commonService: CommonService, private productService: ProductStateService) {



    }

    ngOnInit(): void {
        this.selectSizeError=false;
        this.commonService.fetchSingleProduct();
        this.productService.productDetailObs$.subscribe(response => {
            if (response) {
                this.productObj = response;
                this.sizeList = this.productObj.size;
            }

        })

        this.galleryOptions = [
            {
                width: '600px',
                height: '400px',
                thumbnailsColumns: 3,
                imagePercent: 100,
                preview: false,
                imageSize: NgxGalleryImageSize.Contain,
                thumbnailSize: NgxGalleryImageSize.Contain,
                imageAnimation: NgxGalleryAnimation.Slide
            },
            // max-width 800
            {
                breakpoint: 800,
                width: '100%',
                height: '600px',
                imagePercent: 80,
                thumbnailsPercent: 20,
                thumbnailsMargin: 20,
                thumbnailMargin: 20
            },
            // max-width 400
            {
                breakpoint: 400,
                preview: false
            }
        ];

        this.galleryImages = [
            {
                small: 'assets/img/myntra_shirt1.png',
                medium: 'assets/img/myntra_shirt1.png',
                big: 'assets/img/myntra_shirt1.png'
            },
            {
                small: 'assets/img/myntra_shirt1.png',
                medium: 'assets/img/myntra_shirt1.png',
                big: 'assets/img/myntra_shirt1.png'
            },
            {
                small: 'assets/img/myntra_shirt1.png',
                medium: 'assets/img/myntra_shirt1.png',
                big: 'assets/img/myntra_shirt1.png'
            }
        ];
    }

    setSelectedSize(event,sizeItem){
        if(this.selectedSize)document.getElementById("size-"+this.selectedSize).classList.remove("selectedButton");
        this.selectedSize=sizeItem;
        this.selectSizeError=false;
        event.currentTarget.classList.add("selectedButton");
    }
    

    addToBag(ev) {
        if(this.selectedSize){       

        }
        else{
            this.selectSizeError=true;
        }
        
    }



}
